* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  min-height: 100vh;
  background: #67a5d4;
}

.main_postings{
  display:block;
  margin: 0 auto;
}

.postings{
  position: relative;
  margin-left: 30%;
  width: 500px;
  background: rgb(35, 35, 35);
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.2);
  padding: 20px;
  margin-bottom: 30px;
  margin-top: 30px;
  border-radius: 10px;
}

.postings_name{
  display: inline-block;
  width: 50%;
  color: rgb(155, 155, 155);
  font-size: 15px;
}

.postings_time{
  display: inline-block;
  width: 50%;
  text-align: right;
  color: rgb(155, 155, 155);
  font-size: 15px;
  margin-bottom: 20px;

}

.postings_title{
  font-size: 23px;
  font-weight: 700;
  margin-bottom: 25px;
  overflow-wrap: break-word;
  color: white;
}

.postings_body{
  font-size: 15px;
  font-weight: 300;
  margin: 15px 15px 30px 15px;
  overflow-wrap: break-word;
  color: white;
}

.postings_like{
  display: inline-block;
  width: 60px;
  color: rgb(155, 155, 155);
  font-size: 15px;
  margin-bottom: 10px;
}

.postings_comment{
  display: inline-block;
  width: 90px;
  color: rgb(155, 155, 155);
  font-size: 15px;
  margin-bottom: 10px;
}

.postings_write{
  display: inline-block;
  width: 60px;
  font-size: 15px;
  color: rgb(155, 155, 155);
}

.postings_delete{
  display: inline-block;
  width: 60px;
  font-size: 15px;
  color: rgb(155, 155, 155);
}

.main_write{
  display: inline-block;
  width: 50%;
  text-align: center;
}

.main_delete{
  display: inline-block;
  width: 50%;
  text-align: center;
}

